/* ----------------------------- */
/* ------- MOBILE Layout ------- */
/* ----------------------------- */
.scholarshipsBackground {
  background-image: url('../assets/graphics/ambitious-creative-co-rick-barrett-110145-unsplash.jpg');
  background-size: cover;
  background-repeat: no-repeat !important;
  background-attachment: fixed;
}

.scholarshipText {
  padding: 1rem 5%;
  margin: 0rem;
  color: white;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.scholarshipLogoControl {
  width: 70%;
  max-width: 250px;
  margin-bottom: 1rem;
  margin-top: 2rem;
}

.listCentering {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.bulletPoints {
  display: -ms-flexbox;
  display: flex;
  text-align: left;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 2rem;
}

/* ----------------------------- */
/* ------- TABLET Layout ------- */
/* ----------------------------- */

@media screen and (min-width: 769px) {
  .scholarshipLogoControl {
    max-width: 300px;
  }
}

/* ----------------------------- */
/* ------- DESKTOP Layout ------ */
/* ----------------------------- */

@media screen and (min-width: 1024px) {
  .scholarshipLogoControl {
    max-width: 400px;
  }
}

